.work {
    width: 450px;
    margin: 30px auto;
    display: block;
    color: #fff !important;
    transition: all 0.3s ease 0s;
    position: relative;
    font-size: inherit;
    background: #252527;
    text-indent: inherit;
    overflow: hidden;
}

.icon {
    height: 260px;
    min-width:100%;
}

img {

}

.button {
    display: flex;
    flex-direction: row;
    background-color: red;
    justify-content: flex-end;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 4px;
    overflow: hidden;
    transition: 0.2s;
    cursor: pointer;
    margin: 0 auto;
}

.button:hover {
    background-color: #2196f3;
    color: #282c34;
    box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
    transition-delay: 1s;
}

.workInfo {
    padding: 25px 20px;
    min-height: 210px;
}

.workTitle {
    font-family: "Josefin Sans", sans-serif;
    font-size: 16px;
    letter-spacing: 1px;
    margin: 0 0 5px;
    color: #ffffff;
}

.description {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    line-height: 1.8;
    font-size: 12px;
    color: red;
}