.myWorksBlock {
    /*min-height: 100vh;*/
    background-color: #1f1f20;
    padding: 100px 0;
}

.myWorksContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

.title {
    margin: 0 0 30px;
    color: #fff;
    background-color: #1f1f20;
    text-align: center;
    position: relative;
}

.title h2 {
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 300;
    font-size: 36px;
    margin: 0;
    line-height: 1.2;
    letter-spacing: 5px;
    text-transform: capitalize;
}
.title::before{
    content: '';
    position: absolute;
    background: #4e93e6;
    width: 55px;
    height: 1px;
    bottom: -30px;
    /*полоску центрируем*/
    left: 50%;
    transform: translate(-50%, 50%);
}

.myWork {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  margin-top: 30px;
}

@media screen and (max-width: 626px) {
    .myWork {
        justify-content: center;
    }
}