.main{
    min-height: 200px;
    background-color: #1f1f20;
}
.mainContainer{
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}
h2 {
    display: block;
    font-family: "Josefin Sans", sans-serif;
    letter-spacing: 3px;
    color: #fff;
    font-size: 25px;
    font-weight: 700;
    line-height: 1.2;
}
.shel{
    padding-top: 40px;
}
ul {
    padding: 0px;
}

.a {
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    font-weight: 400;
    font-stretch: normal;
    font-size: 15px;
    line-height: 1.8;
    font-family: Poppins, sans-serif;
}
.a:hover {
    text-transform: none;
    background-color: #2196f3;
    color: #ffffff;
    box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
    transition-delay: 0.1s;
}
.icons{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}
p{
    font-family: Poppins, sans-serif;
    color: #fff;
    font-weight: 300;
    font-size: 13px;
}

