.mainBlock {
    min-height: 100vh;
    background-color: #1f1f20;
    padding: 100px 0px;
}

.particles {
    width: 100%;
    height: 100%;
    pointer-events: initial;
    position: absolute;
    opacity: 0.3;
}

.text {
    width: 400px;
    text-align: left;
    padding-left: 50px;
    background-color: #1f1f20;
}

.intro {
    font-size: 20px;
    color: #fff;
    font-weight: 400;

    line-height: 1.8;
    font-family: Poppins, sans-serif;
}

.text h1 {
    font-size: 50px;
    /*padding: 10px 0 10px;*/
}

.text h1 span {
    font-size: 50px;
    position: relative;
    display: inline-block;
    z-index: 0;
}

h1 {
    font-weight: 700;
    font-size: 40px;
    line-height: 1.2;
    font-family: "Josefin Sans", sans-serif;
    margin: 0;
    color: #fff;
}

.text p, .text p span {
    display: inline-block;
    font-size: 27px;
    margin: 0;
    color: #fff;
    font-weight: 400;
    font-stretch: normal;
    line-height: 1.8;
    font-family: Poppins, sans-serif;
    padding: 2px;
}

.text h1 span::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 20px;
    width: 100%;
    z-index: -1;
    background: #4e93e6;
}

.photo {
    width: 290px;
    height: 400px;
    z-index: 1;
    position: relative;


}

img {

    /*max-height: 430px;*/
    /*max-width: 100%;*/
    /*height: auto;*/
    /*vertical-align: middle;*/
    /*border-style: none;*/
}

.heroBanner {
    padding-top: 10px;
    /*display: table-cell !important;*/
    /*width: 40%;*/
    /*vertical-align: middle;*/
}

.heroRight {
    position: relative;
}


.heroRight span {
    position: absolute;
    top: -18px;
    bottom: 0;
    z-index: 0;
    height: 105%;
    width: 100%;
    border: 5px solid #4e93e6;
    left: 15px;
    right: 0;
}


@media screen and (max-width: 680px) {
    .mainBlock {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
}
