.contactBlock {
    height: 70%;
    background-color: #252527;
    padding: 100px 0px;
}

.contactContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.block {
    opacity: 0.71;
    box-shadow: 0 0 30px 0 rgba(21, 21, 21, 0.12);
    height: 80%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.ajaxBlock {
    width: 98%;
    height: 10%;
}

.formBlock {
    width: 70%;
    padding-top: 60px;
    padding-right: 15px;
    padding-left: 15px;
}

.formGroup {
    visibility: visible;
    animation-delay: 0.2s;
    animation-name: fadeInUp;
    margin-bottom: 1rem;
}

textarea {
    overflow: auto;
    resize: vertical;
}

textarea.message {
    /*padding: 15px;*/
    /*height: auto;*/
    /*margin-left: -13px;*/
}

.emailName {
    display: block;
    width: 96%;
    height: 32px;
    color: silver;
    background-color: #1f1f20;
    border: 1px solid #4a4a4a;
    font-family: Poppins, sans-serif;
    font-weight: 400;
    line-height: 1.5;
    font-size: 15px;
    padding: 10px;
}

.message {
    display: block;
    width: 96%;
    height: 145px;
    color: silver;
    background-color: #1f1f20;
    border: 1px solid #4a4a4a;
    font-family: Poppins, sans-serif;
    font-size: 15px;
    font-weight: 400;
    font-stretch: normal;
    line-height: 1.8;
    padding: 10px;
}

.send {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-top: 50px;
    border: none;
    cursor: pointer;
    border-radius: 2px;
    background-color: #4e93e6;
    height: 30px;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    font-stretch: normal;
    line-height: 1.8;
}

.send:hover {
    text-transform: none;
    background-color: #2196f3;
    color: #ffffff;
    box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
    transition-delay: 0.1s;
}

