.skillsBlock {
  height: 70%;
  background-color: #252527;
  padding: 100px 0px;
  /*min-height: 100vh;*/
  /*background-color: #252527;*/
}
.skillsContainer{
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 15px;
}

.skills {
  /*border: 1px solid black;*/
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 30px;
}
@media screen and (max-width: 970px) {
  .skills {
    justify-content: center;
  }
}


